.overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.75);

  .modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    height: 90vh;
    aspect-ratio: 16 / 9;
    background-color: #fff;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
    }
  }
}
